@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@1,600;1,700&display=swap);
*{
  padding: 0;
  font-family: 'Mulish', sans-serif;
  box-sizing: border-box;
}
.navbar{
  background-color: rgb(243, 158, 158);
  border-radius: 5px;
}

.navbar-brand{
  font-size: 1.9rem;
  color: #fff;
}
.nav-item{
  text-decoration: none;
}



.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
a.menu_active{
  font-size: bold;
  border-bottom: 3px solid rgb(212, 34, 34);
}
.img-fluid{
  border-radius: 5px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.card-img-top{
  width: 300px;
  height: 140px;

}

.card{
  width: 300px;
  height: 325px;
}




